@import url(https://fonts.googleapis.com/css?family=Lato:100,300,300i,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #fff;
  color: #555;
  font-family: -apple-system, BlinkMacSystemFont, "Lato", "Roboto", sans-serif;
  font-size: 62.5%;
  font-weight: 300;
  overflow-x: hidden;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3 {
  font-weight: 300;
  text-transform: uppercase;
}

h1 {
  margin-top: 0;
  margin-bottom: 2rem;
  color: #fff;
  font-size: 3.4rem;
  letter-spacing: 1px;
  word-spacing: 4px;
}

h2 {
  text-align: center;
  font-size: 2.8rem;
  word-spacing: 2px;
  letter-spacing: 1px;
  margin: 4rem 0;
}

a {
  cursor: pointer;
}

h3 {
  font-size: 2.2rem;
}

h2::after {
  display: block;
  height: 2px;
  width: 10rem;
  background-color: #e67e22;
  content: "";
  margin: 0 auto;
  margin-top: 2rem;
}

.btn:link,
.btn,
.btn:visited {
  display: inline-block;
  font-weight: 300;
  border-radius: 20rem;
  font-size: 2rem;
  padding: 1rem 3rem;
  text-decoration: none;
  -webkit-transition: background-color 0.4s, border 0.4s, color 0.4s;
  transition: background-color 0.4s, border 0.4s, color 0.4s;
  margin-bottom: 1rem;
}

.btn:hover,
.btn:active {
  background-color: #cf6d17;
  color: #fff;
}

.btn-full:link,
.btn-full,
.btn-full:visited {
  background-color: #e67e22;
  color: #fff !important;
  margin-right: 1rem;
}

.btn-ghost,
.btn-ghost:link,
.btn-ghost:visited {
  border: 1px solid #e67e22;
  color: #e67e22 !important;
}

.btn-full,
.btn-full:hover,
.btn-full:active {
  border: 1px solid #cf6d17;
}

.btn-ghost:hover,
.btn-ghost:active {
  border: 1px solid #cf6d17;
  color: #ffffff !important;
}

.icon-small {
  width: 30px;
  text-align: center;
  margin-right: 1rem;
}

@media only screen and (min-width: 320px) {
  .row {
    margin: 0;
  }
  section {
    padding: 0;
  }
}

@media only screen and (min-width: 576px) {
  section {
    padding: 1rem 0;
  }

  .row {
    margin: 15px 0;
  }
}

@media only screen and (min-width: 768px) {
  section {
    padding: 4rem 0;
  }
}

@media only screen and (min-width: 1024px) {
  .active-link {
    border-bottom: 1px solid #cf6d17;
  }
}

@media only screen and (min-width: 1367px) {
  html {
    font-size: 75%;
  }
  .icon-small {
    width: 40px;
    text-align: center;
    margin-right: 1rem;
  }
}

@media only screen and (min-width: 1440px) {
  html {
    font-size: 87.5%;
  }
  .container {
    max-width: 1350px;
  }
  .icon-small {
    width: 45px;
    text-align: center;
    margin-right: 1rem;
  }
}

@media only screen and (min-width: 1920px) {
  html {
    font-size: 100%;
  }
  .container {
    max-width: 1800px;
  }
  .icon-small {
    width: 55px;
    text-align: center;
    margin-right: 1rem;
  }
}

@media only screen and (min-width: 2560px) {
  html {
    font-size: 125%;
  }
  .container {
    max-width: 1800px;
  }
  .icon-small {
    width: 65px;
    text-align: center;
    margin-right: 1rem;
  }
}


header {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))),
    url(/omnifood/static/media/hero.6923d550.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/omnifood/static/media/hero.6923d550.jpg);
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-attachment: fixed;
  position: relative;
}

@media only screen and (min-width: 320px) {
  header {
    height: 110vh;
  }
}

@media only screen and (min-width: 450px) {
  header {
    height: 100vh;
  }
}



.nav {
  height: 120px;
}

.scrolled {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  background-color: #fff;
  height: 70px;
  width: 100vw;
}

.logo-scrolled {
  height: 60px;
  margin-top: 0;
}

.navbar-toggler-icon {
  font-size: 24px;
}

.navbar-collapse {
  text-align: center;
}

.navigation-link {
  color: #fff;
  font-size: 1.75rem;
  text-transform: uppercase;
  margin-right: 1rem;
}

@media only screen and (min-width: 320px) {
  .logo {
    height: 60px;
  }
  .navigation-link {
    padding: 0 1rem;
  }
  .navbar-brand {
    margin-left: 1rem;
  }
  .nav-scroll {
    background-color: #fff;
  }
  .navbar {
    padding: 0;
  }
  .navigation-link{
    margin-bottom: 1rem;
  }
  .navbar-toggler {
    margin-right: 3rem;
  }
}

@media only screen and (min-width: 768px) {
  .navbar-collapse {
    margin-top: 0;
  }

  .navigation-link {
    padding: .5rem 1rem;
    margin-bottom: 0;
  }
  .logo {
    height: 100px;
    margin-top: 40px;
  }
}

@media only screen and (min-width: 1024px) {
  .navigation-link:link,
  .navigation-link:visited {
    border-bottom: 2px solid transparent;
    -webkit-transition: border-bottom 0.2s;
    transition: border-bottom 0.2s;
    padding: 8px;
  }
  .navigation-link:hover,
  .navigation-link:active {
    border-bottom: 2px solid #e67e22;
  }
}

.hero-text-box {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

@media only screen and (min-width: 320px) {
  .hero-text-box {
    left: 50%;
    -webkit-transform: translate(-50%, -10%);
            transform: translate(-50%, -10%);
    text-align: center;
    width: 90%;
  }
}

@media only screen and (min-width: 375px) {
  .hero-text-box {
    -webkit-transform: translate(-50%, -25%);
            transform: translate(-50%, -25%);
  }
}

@media only screen and (min-width: 576px) {
  .hero-text-box {
    left: unset;
    -webkit-transform: translate(0, -30%);
            transform: translate(0, -30%);
    text-align: unset;
    width: 100%;
  }
}


.icon {
  height: 7rem;
  width: 7rem;
  display: block;
  margin-bottom: 1rem;
  color: #e67e22;
}

.paragraph {
  width: 70%;
  margin-left: 15%;
  font-size: 1.8rem;
  text-align: center;
}

.features {
  text-align: center;
}

.features-section .paragraph {
  margin-bottom: 3rem;
}

.box {
  padding: 1%;
  display: flex;
  flex-direction: column;
align-items: center;
}

.box p {
  font-size: 1.6rem;
}

.meals-section {
  height: 100%;
  padding: 0;
}

.meal-col {
  padding: 0;
}

.meal-row {
  margin: 0;
}

.meal-photo {
  margin: 0;
  overflow: hidden;
  background-color: #000;
}

.meal-img {
  max-width: 100%;
  height: auto;
}

@media only screen and (min-width: 1024px) {

  .meal-img {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    opacity: 0.7;
    -webkit-transition: 0.5s;
    transition: 0.5s;

  }
  .meal-img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.steps-section {
  background-color: #f4f4f4;
}

.steps-container {
  margin: 0;
  margin-top: 4rem;
}

.app-col,
.steps-col {
  height: auto;
}

.app-col {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.app-screen {
  margin-bottom: 10px;
}

.steps-col {
  display: flex;
  flex-direction: column;
}

.steps-box {
  display: flex;
  width: 80%;
}

.step-number {
  color: #e67e22;
  font-size: 3rem;
  height: 54px;
  width: 54px;
  border: 2px solid #e67e22;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  margin-right: 25px;
}

.steps {
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

.app-btn {
  border: none;
  background-color: inherit;
}

.app-btn img {
  height: 4rem;
  width: 13rem;
  margin: 0 1rem;
}

@media only screen and (min-width: 320px) {
  .app-col {
    justify-content: center;
    margin-bottom: 1.5rem;
  }

  .app-links {
    display: flex;
    margin-top: 10px;
    flex-direction: column;
  }

  .app-screen {
    max-width: 45%;
  }

  .steps-box {
    width: 100%;
  }

  .step-number {
    padding: 5px 15px;
    font-size: 2.5rem;
    margin-right: 12px;
  }

  .app-btn {
    margin-bottom: 15px;
  }

  .steps {
    font-size: 1.6rem;
  }

  .steps-section {
    padding: 2rem;
  }
  .steps-box {
    margin: 5px 0;
  }
}

@media only screen and (min-width: 375px) {
  .app-links {
    flex-direction: row;
    justify-content: center;
  }
}

@media only screen and (min-width: 576px) {
  .steps-box {
    margin: 10px 0;
  }
  .step-number {
    margin-right: 1.5rem;
  }
}

@media only screen and (min-width: 768px) {
  .app-col {
    justify-content: flex-end;
    margin-bottom: 0;
  }
  .steps-col {
    justify-content: space-evenly;
  }
  .steps-box {
    margin: 0;
  }
  .steps {
    font-size: 1.8rem;
  }
  .steps-section {
    padding: 4.5rem;
  }
}

@media only screen and (min-width: 800px) {
  .app-screen {
    max-width: 40%;
  }
}

@media only screen and (min-width: 1024px) {
  .steps-box {
    width: 80%;
  }
  .app-links {
    justify-content: flex-start;
  }
}

@media only screen and (min-width: 1300px) {
  .step-number {
    font-size: 2.8rem;
    padding-top: 0;
    padding-bottom: 50px;
  }
}

@media only screen and (min-width: 1400px) {
  .step-number {
    padding-bottom: 60px;
  }
}

@media only screen and (min-width: 1920px) {
  .step-number {
    width: 75px;
    height: 70px;
    padding-bottom: 75px;
  }
}

@media only screen and (min-width: 2560px) {
  .step-number {
    height: 85px;
  }
}

.city-img {
  max-width: 100%;
  height: auto;
}

.city-details {
  margin: 2rem 0;
}

.city-feature {
  margin-top: 1rem;
  font-size: 1.8rem;
}

.city-name {
  margin-bottom: 2rem;
}

.twitter-link:link,
.twitter-link:visited {
  color: #e67e22;
  text-decoration: none;
  padding-bottom: 1px;
  border-bottom: 1px solid #e67e22;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.twitter-link:hover,
.twitter-link:active {
  color: #555;
  border-bottom: 1px solid transparent;
}
.testimonial-section {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))),
    url(/omnifood/static/media/back-customers.a17c3da3.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/omnifood/static/media/back-customers.a17c3da3.jpg);
  color: #fff;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  margin-top: 2rem;
}

.testimonial-heading {
  padding-top: 2rem;
}

.customer-testimonials {
  font-style: italic;
  padding: 2%;
  font-size: 2rem;
  position: relative;
  margin-top: 4rem; 
}

.customer-testimonials::before {
  content: "\201c";
  font-size: 8rem;
  display: block;
  position: absolute;
  top: -5rem;
  left: -5px;
}

.customer-cite {
  font-size: 1.8rem;
  margin-top: 2.5rem;
  display: block;
}

.customer-img {
  border-radius: 50%;
  height: 45px;
  margin-right: 1rem;
  vertical-align: middle;
}

@media only screen and (min-width: 1367px) {
  .customer-img {
    height: 50px;
  }
}

@media only screen and (min-width: 1440px) {
  .customer-img {
    height: 60px;
  }
}

@media only screen and (min-width: 1920px) {
  .customer-img {
    height: 65px;
  }
}

@media only screen and (min-width: 2560px) {
  .customer-img {
    height: 70px;
  }
}
.card-heading {
