}

.price-caption {
  font-size: 1.6rem;
  margin: 1rem 0;
}

@media only screen and (min-width: 320px) {
  .pricing {
    font-size: 3.6rem;
  }

  .pricing span {
    font-size: 1.8rem;
  }
}

@media only screen and (min-width: 576px) {
